        h3 Fases de un ensilaje
    
    .bg_img
      p Las fases de un ensilaje son los procesos mediante los cuales se conserva forraje en un ambiente anaerbico. Sirven para preservar los nutrientes del forraje, asegurando alimento de calidad en pocas de escasez. Funcionan en cuatro etapas: fase aerbica, fase de fermentacin, fase estable y fase de deterioro, donde los microorganismos ayudan a conservar el alimento adecuadamente. A continuacin, se explica las funciones de cada fase:

      .row.mb-5.justify-content-center
        .col-md-8.col-lg-4.mb-3
          figure
            img(src='@/assets/curso/temas/tema4/11.png', alt='Texto que describa la imagen')
        .col-lg-8.align-content-center
          //- LineaTiempoD debe ir acompaado de una de una de estas clases => 
          //- .color-primario, .color-secundario, .color-acento-contenido, .color-acento-botones
          LineaTiempoD.color-secundario
            p.text-small(numero="1" titulo="Fase aerbica") En esta fase que dura slo pocas horas, el oxgeno atmosfrico presente en la masa vegetal disminuye rpidamente debido a la respiracin de los materiales vegetales y a los microorganismos aerbicos y aerbicos facultativos como las levaduras y las enterobacterias. Adems, hay una actividad importante de varias enzimas vegetales, como las proteasas y las carbohidrasas, siempre que el pH se mantenga en el rango normal para el jugo del forraje fresco (pH 6,5 - 6,0).
            p.text-small(numero="2" titulo="Fase de fermentacin") Esta fase comienza al producirse un ambiente anaerbico. Dura de varios das hasta varias semanas, dependiendo de las caractersticas del material ensilado y de las condiciones en el momento del ensilaje. Si la fermentacin se desarrolla con xito, la actividad BAC proliferar y se convertir en la poblacin predominante. A causa de la produccin de cido lctico y otros cidos, el pH bajar a valores entre 3,8 a 5,0.
            p.text-small(numero="3" titulo="Fase estable") Mientras se mantenga el ambiente sin aire, ocurren pocos cambios. La mayora de los microorganismos de la Fase 2 lentamente reducen su presencia. Algunos microorganismos acidfilos sobreviven este periodo en estado inactivo; otros, como clostridios y bacilos, sobreviven como esporas. Slo algunas proteasas y carbohidrasas, y microorganismos especializados, como Lactobacillus buchneri que toleran ambientes cidos, continan activos, pero a menor ritmo.
            p.text-small(numero="4" titulo="Fase de deterioro aerbico") Esta fase comienza con la apertura del silo y la exposicin del ensilaje al aire. Esto es inevitable cuando se requiere extraer y distribuir el ensilaje, pero puede ocurrir antes de iniciar la explotacin por dao de la cobertura del silo (p. ej. roedores o pjaros). El periodo de deterioro puede dividirse en dos etapas. La primera se debe al inicio de la degradacin de los cidos orgnicos que conservan el ensilaje, por accin de levaduras y ocasionalmente por bacterias que producen cido actico. Esto induce un aumento en el valor del pH, lo que permite el inicio de la segunda etapa de deterioro; en ella se constata un aumento de la temperatura y la actividad de microorganismos que deterioran el ensilaje, como algunos bacilos. La ltima etapa tambin incluye la actividad de otros microorganismos aerbicos -tambin facultativos- como mohos y enterobacterias. El deterioro aerbico ocurre en casi todos los ensilajes al ser abiertos y expuestos al aire. Sin embargo, la tasa de deterioro depende de la concentracin y de la actividad de los organismos que causan este deterioro en el ensilaje. (Oude, Driehuis, Gottschal y Spoelstra, s.f.) 
    
