          p.mb-0 Los bovinos en el rumen desdoblan la urea convirtindola en protena. Para que el animal se adapte al consumo de este elemento se debe suministrar por niveles que van en la primera semana 25 %, en la segunda semana 50 %, en la tercera semana 75 % y en la cuarta semana se suministra un 100 %; si en este proceso de adaptacin de la urea se deja de dar al animal por varios das, se debe empezar de nuevo el suministro.
      
      p La urea que se usa para la alimentacin bovina se debe disolver en agua tibia y luego se adiciona al pasto picado. La urea siempre se debe usar con una fuente de energa, por eso se puede mezclar con la miel o con el forraje de corte, lo importante es distribuirla bien y que los animales reciban la cantidad adecuada sin que exista peligro de intoxicacin. Si esto llegara a pasar se recomienda utilizar vinagre.

      .row.justify-content-center
        .col-lg-8
          p Un buen nivel de urea debe estar entre 60 y 100 g/animal/da, el nivel mximo de suministro de urea es 135 g/animal/da, este abastecimiento debe tener en cuenta el tamao del novillo y los dems componentes de la dieta.
