getales y a los microorganismos aerbicos y aerbicos facultativos como las levaduras y las enterobacterias. Adems, hay una actividad importante de varias enzimas vegetales, como las proteasas y las carbohidrasas, siempre que el pH se mantenga en el rango normal para el jugo del forraje fresco (pH 6,5 - 6,0).")]),_c('p',{staticClass:"text-small",attrs:{"numero":"2","titulo":"Fase de fermentacin"}},[_vm._v("Esta fase comienza al producirse un ambiente anaerbico. Dura de varios das hasta varias semanas, dependiendo de las caractersticas del material ensilado y de las condiciones en el momento del ensilaje. Si la fermentacin se desarrolla con xito, la actividad BAC proliferar y se convertir en la poblacin predominante. A causa de la produccin de cido lctico y otros cidos, el pH bajar a valores entre 3,8 a 5,0.")]),_c('p',{staticClass:"text-small",attrs:{"numero":"3","titulo":"Fase estable"}},[_vm._v("Mientras se mantenga el ambiente sin aire, ocurren pocos cambios. La mayora de los microorganismos de la Fase 2 lentamente reducen su presencia. Algunos microorganismos acidfilos sobreviven este periodo en estado inactivo; otros, como clostridios y bacilos, sobreviven como esporas. Slo algunas proteasas y carbohidrasas, y microorganismos especializados, como Lactobacillus buchneri que toleran ambientes cidos, continan activos, pero a menor ritmo.")]),_c('p',{staticClass:"text-small",attrs:{"numero":"4","titulo":"Fase de deterioro aerbico"}},[_vm._v("Esta fase comienza con la apertura del silo y la exposicin del ensilaje al aire. Esto es inevitable cuando se requiere extraer y distribuir el ensilaje, pero puede ocurrir antes de iniciar la explotacin por dao de la cobertura del silo (p. ej. roedores o pjaros). El periodo de deterioro puede dividirse en dos etapas. La primera se debe al inicio de la degradacin de los cidos orgnicos que conservan el ensilaje, por accin de levaduras y ocasionalmente por bacterias que producen cido actico. Esto induce un aumento en el valor del pH, lo que permite el inicio de la segunda etapa de deterioro; en ella se constata un aumento de la temperatura y la actividad de microorganismos que deterioran el ensilaje, como algunos bacilos. La ltima etapa tambin incluye la actividad de otros microorganismos aerbicos -tambin facultativos- como mohos y enterobacterias. El deterioro aerbico ocurre en casi todos los ensilajes al ser abiertos y expuestos al aire. Sin embargo, la tasa de deterioro depende de la concentracin y de la actividad de los organismos que causan este deterioro en el ensilaje. (Oude, Driehuis, Gottschal y Spoelstra, s.f.) ")])])],1)])]),_vm._m(9),_vm._m(10),_vm._m(11),_vm._m(12),_c('SlyderF',{attrs:{"columnas":"col-md-6 col-xl-3"}},[_c('div',{staticClass:"tarjeta--boton bgr_5 p-4 h-100"},[_c('div',{staticClass:"row justify-content-center mb-3"},[_c('div',{staticClass:"col-7"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema4/14.svg"),"alt":"Texto que describa la imagen"}})])])]),_c('h3',{staticClass:"text-center"},[_vm._v("Forraje")]),_c('p',{staticClass:"text-small text-center"},[_vm._v("El forraje para ensilar debe tener el tiempo de cosecha requerido para que est en el punto ptimo de nutrientes.")])]),_c('div',{staticClass:"tarjeta--boton bgr_6 p-4 h-100"},[_c('div',{staticClass:"row justify-content-center mb-3"},[_c('div',{staticClass:"col-7"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema4/15.svg"),"alt":"Texto que describa la imagen"}})])])]),_c('h3',{staticClass:"text-center"},[_vm._v("Material")]),_c('p',{staticClass:"text-small text-center"},[_vm._v("Picar el material en trozos bien pequeos de 1 a 3 cms.")])]),_c('div',{staticClass:"tarjeta--boton bgr_5 p-4 h-100"},[_c('div',{staticClass:"row justify-content-center mb-3"},[_c('div',{staticClass:"col-7"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema4/16.svg"),"alt":"Texto que describa la imagen"}})])])]),_c('h3',{staticClass:"text-center"},[_vm._v("Ensilaje")]),_c