<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5.mb-5
    .titulo-principal.color-acento-contenido
      .titulo-principal__numero
        span 3
      h1 Requerimientos nutricionales

    .row.mb-5.justify-content-center
      .col-lg-8.col-xl-5.mb-3
        figure
          img(src='@/assets/curso/temas/tema3/1.png', alt='Texto que describa la imagen')
      .col-xl-7
        p Los nutrientes en los rumiantes sirven para dar energa metablica al animal, ayudan en su desarrollo y crecimiento, contribuye a la reparacin de los tejidos corporales y favorece la reproduccin.
        .cajon.color-acento-contenido.p-4.mb-5
          p.mb-0 Los bovinos tienen necesidades nutricionales especficas dependiendo de la composicin gentica, corporal, talla y estado sexual (hembra o macho).
    
    .bg_img
      .row.mb-5.justify-content-center
        .col-lg-10
          .titulo-sexto.color-acento-contenido.mb-5
            h5 Figura 2. 
            span <em>Requerimientos nutricionales de los bovinos</em>
          figure
            img.mb-3(src='@/assets/curso/temas/tema3/2.svg', alt='Los requerimientos nutricionales como la ingesta, materia seca, agua, energa, protena, fibra, vitaminas y minerales. La alimentacin bovina en cuanto al manejo de praderas, sea continuo, alterno y rotacional. En cuanto a los forrajes, protena, energa, agua, vitaminas y minerales. La suplementacin es fibroso, proteicos y energticos; y la conservacin de forrajes en ensilaje.') 
            
    p.mb-5 Para que un animal tenga un estado nutricional equilibrado, se debe tener en cuenta:

    .row.mb-5
      .col-md-6.col-lg-3.mb-3
        div.tarjeta.text-center.bgr_10.p-4.h-100
          figure.mb-5.d-flex.justify-content-center
            img.pst_3(src='@/assets/curso/temas/tema3/3.png', alt='Imagen decorativa')
          h2 Energa
          p Suministrar la energa suficiente para que todas las funciones metablicas y corporales acten correctamente.
      .col-md-6.col-lg-3.mb-3
        div.tarjeta.text-center.bgr_6.p-4.h-100
          figure.mb-5.d-flex.justify-content-center
            img.pst_3(src='@/assets/curso/temas/tema3/4.png', alt='Imagen decorativa')
          h2 Protenas
          p Proveer la cantidad de protenas suficientes para evitar que el animal tenga deficiencias corporales.
      .col-md-6.col-lg-3.mb-3
        div.tarjeta.text-center.bgr_10.p-4.h-100
          figure.mb-5.d-flex.justify-content-center
            img.pst_3(src='@/assets/curso/temas/tema3/5.png', alt='Imagen decorativa')
          h2 Agua
          p Abastecer agua y minerales en una cantidad adecuada para que el animal compense las prdidas por el trabajo realizado en el desplazamiento de un potrero a otro.
      .col-md-6.col-lg-3.mb-3
        div.tarjeta.text-center.bgr_6.p-4.h-100
          figure.mb-5.d-flex.justify-content-center
            img.pst_3(src='@/assets/curso/temas/tema3/6.png', alt='Imagen decorativa')
          h2 Vitaminas
          p Proporcionar las vitaminas esenciales para los bovinos, puesto que estas no son producidas por el animal.
    
    .bg_img_1
      .row.mb-5.justify-content-center
        .col-auto.col-md-2
          figure
            img(src='@/assets/curso/temas/tema3/7.png', alt='Imagen decorativa')
        .col-10.align-content-center
          p.mb-0 Los requerimientos nutricionales de los bovinos varan segn su etapa de crecimiento, produccin y ambiente. A continuacin, se explican los requerimientos nutricionales:
    
    AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta bgr_2")
      div(titulo="Ingesta")
        .row.mb-3.justify-content-center
          .col-md-6.col-lg-3.mb-3
            figure
              img(src='@/assets/curso/temas/tema3/8.png', alt='Texto que describa la imagen')
          .col-lg-9.mb-4.mb-lg-0
            p La ingesta es el consumo de alimentos por el animal y es muy importante porque incluye los nutrientes que contribuyen en el desarrollo del rumiante y en todas las etapas de crecimiento.
            p En el suministro de alimentos a los bovinos se deben tener en cuenta los siguientes factores:
            ul.lista-ul--color.lista-ul--separador
              li 
                i.fas.fa-check-circle.bgr_14 
                | Caractersticas nutricionales del alimento.
              li
                i.fas.fa-check-circle.bgr_14
                | Disponibilidad y cantidad de forraje a proporcionar.
              li 
                i.fas.fa-check-circle.bgr_14
                | Cantidad de energa y fibra que contiene el alimento.
              li 
                i.fas.fa-check-circle.bgr_14
                | Estado fisiolgico del bovino.
              li 
                i.fas.fa-check-circle.bgr_14
                | Palatabilidad.
        .row.justify-content-center
          .col-lg-9.mb-3
            p Es importante que cuando le suministre al ganado bovino el alimento sea agradable a su paladar, porque el animal evita los sabores amargos y prefiere los dulces.
            p Cuando se provee una ingesta adecuada se garantiza una buena nutricin para el rumiante y se ve reflejada en alta produccin tanto de carne como de leche, por lo tanto, es importante que verifique un elevado consumo de forraje teniendo en cuenta los siguientes aspectos:
            ul.lista-ul--color.lista-ul--separador
              li 
                i.fas.fa-check-circle.bgr_14 
                | Forraje: mantener el forraje en estado vegetativo por medio del pastoreo.
              li
                i.fas.fa-check-circle.bgr_14
                | Diversificar: diversificar la pradera incluyendo diferentes especies de pastos y un 30 % de leguminosas.
              li 
                i.fas.fa-check-circle.bgr_14
                | Conservar: conservar la pradera densa para que los animales tengan mayor disponibilidad de alimento.
          .col-md-6.col-lg-3
            figure
              img(src='@/assets/curso/temas/tema3/10.png', alt='Texto que describa la imagen')
      .row.mb-3(titulo="Materia seca").justify-content-center
        .col-md-6.col-lg-2.mb-3
          figure
            img(src='@/assets/curso/temas/tema3/9.png', alt='Texto que describa la imagen')
        .col-lg-10.mb-4.mb-lg-0
          p Los bovinos deben consumir entre el 2 % y el 3 % de materia seca en relacin con su peso vivo, de la cual dos tercios se suministran en forma de forraje.
          p El ganado crecer ms dependiendo de la cantidad y calidad de los nutrientes que se le suministre, es decir, que, si se le proporcionan alimentos ricos en energa y fibra, pero pobres en protena, el animal no crecer de la misma manera que cuando se le dan todos los nutrientes necesarios para su ptimo desarrollo. El ganado bovino necesita de seis nutrientes o componentes esenciales en su racin diaria:
          .row
            .col-lg-3
              ul.lista-ul--color.lista-ul--separador
                li 
                  i.fas.fa-check-circle.bgr_14 
                  | Agua.
                li
                  i.fas.fa-check-circle.bgr_14
                  | Energa.
            .col-lg-3
              ul.lista-ul--color.lista-ul--separador
                li 
                  i.fas.fa-check-circle.bgr_14 
                  | Protenas.
                li
                  i.fas.fa-check-circle.bgr_14
                  | Fibra.
            .col-lg-3
              ul.lista-ul--color.lista-ul--separador
                li 
                  i.fas.fa-check-circle.bgr_14 
                  | Vitaminas.
                li
                  i.fas.fa-check-circle.bgr_14
                  | Minerales.

    p.mb-5 A continuacin, se describe cada uno de estos nutrientes:

    h3.bgr_8 Agua          

    .row.mb-5.mt-4.justify-content-center
      .col-lg-8.col-xl-5.mb-3
        figure
          img(src='@/assets/curso/temas/tema3/11.png', alt='Texto que describa la imagen')
      .col-lg-12.col-xl-7
        p La cantidad de agua que necesita un bovino obedece a factores tales como: edad, tamao corporal, clima, consumo de materia seca y estado fisiolgico; tambin depende de la temperatura en la cual se encuentre el animal.
        p Por ejemplo, en pocas de invierno van a consumir alrededor de un galn por cada 50 kg de peso corporal, mientras que en climas clidos pueden ingerir cerca de dos galones por cada 50 kg. Para los bovinos que estn en lactancia se aconseja suministrar agua limpia y fresca.
        .cajon.color-acento-contenido.p-4.mb-5
          p.mb-0 En la siguiente tabla se muestra la cantidad de agua que necesitan los bovinos dependiendo de su periodo de produccin y edad:
    
    .bg_img
      .row.mb-5.justify-content-center
        .col-lg-8
          .titulo-sexto.color-acento-contenido.mb-5
