      h3.bgr_8 Energa
      .row.mb-5.mt-4
        .col-lg-7
          p La energa es importante en la nutricin de los bovinos porque facilita el desarrollo y crecimiento del animal, esta proviene de los carbohidratos, las protenas y las grasas.
          p En la racin suministrada al rumiante, es importante asegurarse de que sea adecuada. Si es insuficiente, las bacterias del rumen no podrn desempear correctamente su funcin en la asimilacin de protenas, lo que provocar una disminucin en la produccin de leche y carne.
        .col-lg-5.align-content-center
          figure.d-flex
            img(src='@/assets/curso/temas/tema3/12.png', alt='Texto que describa la imagen')

    h3.bgr_8 Protenas
    p.mt-4 Las protenas son indispensables para los bovinos cuando el animal se encuentra en periodo de crecimiento y produccin, adems aportan los aminocidos esenciales tanto para el animal como para los microorganismos del rumen.

    .row.mb-5.justify-content-center
      .col-lg-8.col-xl-5.mb-3
        figure
          img(src='@/assets/curso/temas/tema3/13.png', alt='Texto que describa la imagen')
      .col-lg-12.col-xl-7
        .cajon.color-acento-contenido.p-4
          p.mb-0 La protena suministrada se calcula en funcin del contenido de nitrgeno presente en los forrajes y se conoce como protena cruda, la cual es de gran importancia para la nutricin del animal.
        .bloque-texto-b.color-secundario.p-4
          .bloque-texto-b__texto
            i.fas.fa-quote-left
            h4.mb-0 El ganado requiere dos tipos de protena en su dieta. Un tipo es degradado en el rumen y es usado para cumplir con los requerimientos de la poblacin microbiana, y el otro se salta el rumen y es usado principalmente para cumplir con los requerimientos nutritivos del animal. 
            span - (Rinehart, 2008)
            i.fas.fa-quote-right
    
    h3.bgr_8 Fibra
    .bloque-texto-g.bgr_1.mt-4.p-3.p-sm-4.p-md-5.mb-5
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/temas/tema3/14.jpg')})`}"
      )
      .bloque-texto-g__texto.p-4
        p.mb-0 La fibra en bovinos aporta celulosa, hemicelulosa y lignina; este ltimo compuesto ayuda al buen funcionamiento ruminal, por tal razn, los bovinos deben consumir una cantidad mnima por da para que haya estimulacin de la saliva y la rumia. Los niveles de fibra que se le deben suministrar a las vacas lecheras estn entre 17 % a 22 % de materia seca; si este es superior de un 22 % la capacidad de asimilacin por parte del animal se ve perjudicada y si es inferior a un 17 %, se reduce el nivel de produccin de leche y de grasa.

    .bg_img
      h3.bgr_8 Vitaminas
      p.mt-4 En la dieta de los bovinos las vitaminas son importantes pues ayudan a que se formen catalizadores biolgicos que intervienen en el crecimiento y desarrollo corporal del animal.

      .row.mb-5.justify-content-center
        .col-md-6.col-lg-4.mb-3
          div.tarjeta.text-center.bgr_10.p-4.h-100
            figure.mb-5.d-flex.justify-content-center
              img.pst_2(src='@/assets/curso/temas/tema3/15.png', alt='Imagen decorativa')
            h2 Vitaminas
            p Las vitaminas de mayor importancia que necesitan los rumiantes son las A, D y E; mientras que las vitaminas B y K son sintetizadas por las bacterias del rumen, por lo tanto, no es necesario suplementarlas.	
        .col-md-6.col-lg-4.mb-3
          div.tarjeta.text-center.bgr_6.p-4.h-100
            figure.mb-5.d-flex.justify-content-center
              img.pst_2(src='@/assets/curso/temas/tema3/16.png', alt='Imagen decorativa')
            h2 Vitamina A
            p Es necesaria en vacas preadas para que las cras nazcan en buen estado, porque cuando hay deficiencia de este compuesto se reduce el apetito del rumiante. Los forrajes ricos en caroteno son precursores de la vitamina A. 
        .col-md-6.col-lg-4.mb-3
          div.tarjeta.text-center.bgr_10.p-4.h-100
            figure.mb-5.d-flex.justify-content-center
              img.pst_2(src='@/assets/curso/temas/tema3/17.png', alt='Imagen decorativa')
            h2 Vitamina D
            p La carencia de esta vitamina en la dieta de los bovinos en crecimiento puede producir raquitismo.
    
    .cajon.color-acento-contenido.p-4.mb-5
      p.mb-0 La vitamina D se puede sintetizar en la piel del animal cuando es expuesto al sol, de manera que para las vacas que producen leche y son criadas en condiciones donde no se recibe casi luz solar, se les debe suministrar entre 5000 a 6000 UI (Unidad Internacional).

    h3.bgr_8 Minerales
    p.mt-4 Los principales minerales que requieren los bovinos son: fsforo, calcio, magnesio, potasio, azufre, hierro, manganeso, cobre, zinc y cobalto. 

