        .cajon.color-acento-contenido.p-4.mb-5
          p.mb-0 Los minerales tienen muchas funciones estructurales en la formacin de huesos y tejidos, adems son indispensables en el rumen para que las bacterias se desarrollen y as puedan degradar el alimento.

