    h3.bgr_8 Manejo de praderas
    p.mt-4 Si desea tener una ganadera productiva es necesario que los pastos que utilice en la alimentacin bovina cuenten con un alto contenido de protenas y de energa para que el animal tenga un buen desarrollo y potencial gentico; por lo tanto, se recomienda hacer una asociacin de gramneas con leguminosas, porque estas aportan los nutrientes necesarios en el pastoreo.
  
    .row.mb-5.justify-content-center
      .col-lg-8.col-xl-5.mb-3
        figure
          img(src='@/assets/curso/temas/tema3/21.png', alt='Texto que describa la imagen')
      .col-lg-12.col-xl-7
        p Las leguminosas aportan las protenas que el animal necesita para mantener una buena produccin de leche y carne. Por esta razn, es necesario asociar gramneas con leguminosas, ya que contribuyen al mejoramiento de las praderas mediante el nitrgeno que aportan. Sin embargo, una vez que en la pradera se tienen estas dos especies, es importante evitar la prdida de forraje debido al sobrepastoreo.
        .cajon.color-acento-contenido.p-4.mb-5
          p.mb-0 Cuando los animales se llevan a pastoreo, en la pradera se debe garantizar que el forraje est en el punto mximo de protena y se logra cuando se encuentra en poca de cosecha, dicho periodo vara dependiendo del tipo de forraje que tenga cada predio; por ejemplo, una gramnea como la Brachiaria aproximadamente est en poca de cosecha entre los 35 a 40 das, lo cual indica que cuenta con un contenido de protenas apropiado.
    
    p En la ganadera existen diferentes tipos de pastoreo:

    .row.mb-5.justify-content-center
      .col-md-6.col-lg-3.mb-3
        div.tarjeta.text-center.bgr_10.p-4.h-100
          figure.mb-5.d-flex.justify-content-center
            img.pst_3(src='@/assets/curso/temas/tema3/22.png', alt='Imagen decorativa')
          h2 Pastoreo selectivo
          p El manejo de praderas mediante pastoreo selectivo optimiza la alimentacin del ganado, promoviendo el crecimiento sostenible y la salud animal.
      .col-md-6.col-lg-3.mb-3
        div.tarjeta.text-center.bgr_6.p-4.h-100
          figure.mb-5.d-flex.justify-content-center
            img.pst_3(src='@/assets/curso/temas/tema3/23.png', alt='Imagen decorativa')
          h2 Pastoreo continuo
          p El manejo de praderas en pastoreo continuo, busca optimizar la calidad del forraje y mantener la salud del ganado.
      .col-md-6.col-lg-3.mb-3
        div.tarjeta.text-center.bgr_10.p-4.h-100
          figure.mb-5.d-flex.justify-content-center
            img.pst_3(src='@/assets/curso/temas/tema3/24.png', alt='Imagen decorativa')
          h2 Pastoreo alterno
          p Mejora la salud del suelo y la calidad del forraje, optimizando la produccin ganadera sostenible.
      .col-md-6.col-lg-3.mb-3
        div.tarjeta.text-center.bgr_6.p-4.h-100
          figure.mb-5.d-flex.justify-content-center
            img.pst_3(src='@/assets/curso/temas/tema3/25.png', alt='Imagen decorativa')
          h2 Pastoreo rotacional
          p Mejora la salud de las praderas al distribuir estratgicamente el ganado para evitar la sobreexplotacin.
      
    p.mb-5 El pastoreo ms conveniente es el rotacional, el cual consiste en dividir el rea en potreros y el animal se va cambiando de uno a otro, esto ayuda a conservar los forrajes de la pradera y el animal va contar con ms alimento.
    
    h3.bgr_8 Forrajes

    .bloque-texto-g.bgr_1.mt-4.p-3.p-sm-4.p-md-5.mb-5
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/temas/tema3/26.jpg')})`}"
      )
      .bloque-texto-g__texto.p-4
        p.mb-0 Los forrajes son una alimentacin muy importante en la dieta del bovino, tanto en volumen como en nutrientes, pues son una fuente importante de fibra que cumple un papel importante para la digestin del rumiante, adems aportan protenas, energa, agua, vitaminas y minerales.

    .row.justify-content-center.mb-5
      .col-lg-12.col-xl-7.mb-3
        p Es importante en el sistema ganadero bovino al momento de iniciar un programa de confinamiento, disponer de buena cantidad y calidad de forrajes. Para tal fin, se usan especies de gramneas como pasto elefante, <em>King grass</em>, Guatemala o <em>Maralfalfa</em>, tambin se puede usar la caa azucarera, el pasto de piso y las leguminosas tales como: <em>Leucaena, Kudz, Crotalarias, Frijolillos, Morera, Nacedero, Estilosantes</em>, entre otros.
        .cajon.color-acento-contenido.p-4.mb-5
          p.mb-0 El consumo de pasto de corte por parte de los bovinos depende del sistema de pastoreo y de los alimentos que se les proporcionen, por ejemplo, un novillo necesita de 7 % a 10 % de su peso en pasto verde, mientras que un toro de 350 kg requiere de 22 a 35 kg de forraje al da.
      .col-lg-8.col-xl-5
        figure
          img(src='@/assets/curso/temas/tema3/27.png', alt='Texto que describa la imagen')
    
    p Aunque los forrajes contienen los componentes esenciales que cada bovino necesita para su desarrollo, aportan poca ganancia de peso al animal. Es decir, este tipo de alimentacin presenta ciertas limitaciones. Por ello, es necesario utilizar otros alimentos que cubran todas las necesidades del ganado. Se pueden suministrar mezclas de elementos que proporcionen los nutrientes necesarios para su desarrollo.
    .bloque-texto-g.bgr_1.mt-4.p-3.p-sm-4.p-md-5.mb-5
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/temas/tema3/28.jpg')})`}"
      )
      .bloque-texto-g__texto.p-4
        p.mb-0 En pocas de sequa, los campos no producen los forrajes suficientes para la alimentacin del ganado, lo que ocasiona prdida de peso y afecta el sistema de produccin. En estos casos, es fundamental proporcionar un buen suministro de alimentos ricos en los componentes que el animal requiere. Tipos de pastos ms utilizados en Colombia:

